export enum OrderStatuses {
  CREATED = 'created',
  PANDING = 'pending',
  DONE = 'done',
  CANCEL = 'cancel',
}

type TOrderStatusData = {
  [K in OrderStatuses]: {
    value: string,
    color: string
  }
};
export const OrderStatusData: TOrderStatusData = {
  [OrderStatuses.CREATED]: {
    value: '',
    color: '#F2F2F3',
  },
  [OrderStatuses.PANDING]: {
    value: '',
    color: '#DBD702',
  },
  [OrderStatuses.DONE]: {
    value: '',
    color: '#00CCCC',
  },
  [OrderStatuses.CANCEL]: {
    value: '',
    color: '#E52B1A',
  },
};

type TOrderFeedStatusData = {
  [K in OrderStatuses]?: {
    title: string,
    color: string
  }
};
export const OrderFeedStatusData: TOrderFeedStatusData = {
  [OrderStatuses.DONE]: {
    title: ':',
    color: '#00CCCC',
  },
  [OrderStatuses.PANDING]: {
    title: ' :',
    color: '#DBD702',
  },
};
